<script setup> 
import { ref, onMounted } from 'vue';

const el = ref();

onMounted (() => {
  const observer = new IntersectionObserver((entries, observer) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        entry.target.classList.remove('scrol__animation');
      }
      else {
        entry.target.classList.add('scrol__animation');
      }
    })
  })

  const a = document.querySelectorAll('.scrol__animation').forEach((item) => {
    observer.observe(item);
  })
})
</script>