<template>
    <section class="header__content">
            <div class="container">
                <div class="header__content-container flex-wrap">
                    <div class="header__content-left">
                        <h1 class="header__title scrol__animation transition">Сабитов Данил, Веб разработчик</h1>
                        <div class="header__content-title">
                            <h2 class="header__title purple scrol__animation transition">Фронтенд</h2>
                            <div class="header__javascript">
                                <p class="header__javascript-text scrol__animation transition">JS</p>
                            </div>
                        </div>
                        <p class="header__text scrol__animation transition">Я Сабитов Данил - джуниор веб разработчик. В мои умения входит: html, css, javaScript, jquery, vue.js, gulp, pug. Немного знаю php и node.js.</p>
                        <a href="resume.pdf" download="resume.pdf" class="button header__button">Резюме</a>
                    </div>
                    <div class="header__content-right">
                        <div class="header__photo-container">
                            <div class="header__elips" ref="elips"></div>
                            <img :src="img" alt="author" class="header__photo scrol__animation transition" @click="show = true; bodyBlock()">
                        </div>
                    </div>
                    <transition-group name="window">
                        <div class="header__modal" v-if="show" @click="show = false; bodyBlock()">
                            <div class="header__modal-container">
                                <img :src="img" alt="Большой автор!" class="big__author">
                                <span class="header__modal-icon" @click="show = false; bodyBlock()"><img src="@/assets/img/author/icon.svg" alt="Закрыть"></span>
                            </div>
                        </div>
                    </transition-group>
                </div>
            </div>
    </section>
</template>

<style lang="scss">
@use "@/assets/style/default.scss" as s;
    .header {
        &__photo {
            height: 640px;
            position: relative;
            cursor: pointer;
            @include s.media (s.$xs) {
                height: 500px;
            }
        }
        &__title {
            font-size: 64px;
            font-family: montsserat-bold;
            max-width: 675px;
            width: 100%;
            @include s.media (s.$md) {
                font-size: 47px;
            }
            @include s.media (s.$xs) {
                font-size: 35px;
            }
        }
        &__content {
            display: flex;
            flex-direction: column;
            flex-grow: 1 !important;
            justify-content: center;
            flex-wrap: wrap;

            &-container {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                @include s.media (s.$lg) {
                    justify-content: center;
                }
            }

            &-title {
                display: flex;
                align-items: flex-end;
            }

            &-right {
                max-width: 500px;
                width: 100%;
                display: flex;
                justify-content: center;
            }
        }
        &__javascript {
            margin: 0 0 0 35px;
            width: 64px;
            height: 64px;
            background: #FCF200;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 10px;
            @include s.media (s.$xs) {
                width: 50px;
                height: 50px;
                font-size: 30px !important;
            }

            &-text {
                font-size: 36px;
                font-family: montsserat-bold;

                @include s.media (s.$xs) {
                    font-size: 30px !important;
                }
            }
        }

        &__text {
            font-size: 30px;
            font-family: montsserat-regular;
            max-width: 735px;
            width: 100%;
            margin-top: 65px;
            @include s.media (s.$md) {
                font-size: 24px;
            }

            @include s.media (s.$xs) {
                font-size: 20px;
            }
        }

        &__button {
            margin-top: 40px;
        }

        &__elips {
            max-width: 460px;
            width: 100%;
            max-height: 460px;
            height: 100%;
            border-radius: 100%;
            background: #3C0061;
            position: absolute;
            z-index: -1 !important;
            right: 225px;
            margin: 0 auto;
            
            @include s.media (s.$lg) {
                left: 0;  right: 0;
                padding: 0 15px;
            }

            &::before, &::after {
                content: '';
                display: block;
                width: 100px;
                height: 100px;
                background: rgba(0, 0, 0, 0.214);
                border-radius: 10px;
                backdrop-filter: blur(5px);
                animation: animationBlurBlock 3s infinite;
                transition: all 100ms;

                @include s.media (s.$xs) {
                    width: 70px;
                    height: 70px;
                }
            }

            &::after {
                position: absolute;
                bottom: 0;
                right: 0;
            }
        }

        &__modal {
            position: fixed;
            top: 0;
            left: 0;
            z-index: 2;
            backdrop-filter: blur(5px);
            max-width: 1910px;
            width: 100%;
            height: 100vh;
            display: flex;
            justify-content: center;
            align-items: center;

            &-container {
                box-shadow: 0px 5px 10px 11px rgba(0, 0, 0, 0.16);
                background: #ffffff;
                max-width: 1200px;
                width: 100%;
                height: 800px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 10px;
                position: relative;

                @include s.media (s.$lg) {
                    margin-top: 100px;
                    backdrop-filter: blur(5px);
                    background: #0000005f;
                    width: 100%;
                    height: calc(100vh - 100px);
                }
            }

            &-icon {
                position: absolute;
                top: 10%;
                right: 10%;
                cursor: pointer;
            }
        }


    }
    .window-enter-active,
    .window-leave-active {
        transition: opacity 0.5s ease;
    }

    .window-enter-from,
    .window-leave-to {
        opacity: 0;
    }
    @keyframes animationBlurBlock {
        0%,100% {
            transform: translateY(0);
        }
        50% {
            transform: translateY(50px);
        }
    }

    .purple {
        color: #3C0061;
        width: auto;
    }

    .big__author {
        height: 80%;
    }
</style>

<script>
import { onMounted } from '@vue/runtime-core';
    export default {
        data: () => ({
            img: require('@/assets/img/author/author.png'),
            show: false
        }),
        methods: {
            bodyBlock () {
                if (this.show === true) {
                    document.querySelector('body').style.overflow = 'hidden';
                }
                else {
                    document.querySelector('body').style.overflow = 'auto';
                }
            },
        },
        mounted () {
            let elipsWidth = this.$refs.elips.clientWidth;

            if (window.innerWidth < 500) {
                this.$refs.elips.style = `height: ${elipsWidth - 15}px;
                                          width: ${elipsWidth - 15}px;`;
            }

        }
    }
</script>

