                                <a href="" class="contacts__link">Мой Телеграм</a>
                            </div>
                            <div class="contacts__container-icon">
                                <ul class="contacts__icon">
                                    <li class="contacts__icon scale__link scrol__animation transition" v-for="icon in icons" :key="icon">
                                        <a :href="icon.href">
                                            <img :src="icon.img" alt="Icon">
                                        </a>
