</template>

<style lang="scss">
@use "@/assets/style/default.scss" as s;
    .contacts {
        &__link {
            font-family: inter-regular;
            font-style: normal;
            font-weight: 400;
            font-size: 36px;
            line-height: 50px;
            color: #000000;
            display: block;

            @include s.media (s.$sm) {
                font-size: 20px;
                line-height: 30px;
            }
        }

        &__icon {
            display: flex;
            justify-content: space-between;
            width: 310px;
        }

        &__input {
            display: block;
            max-width: 500px;
            width: 100%;
            height: 30px;
            border: 2px solid #000000;
            border-top: 0;
            border-right: 0;
            border-left: 0;
            outline: 0;
            font-family: inter-regular;
            font-style: normal;
            font-weight: 400;
            font-size: 24px;
            line-height: 50px;
            color: #000000;

            &::placeholder {
                font-family: inter-regular;
                font-style: normal;
                font-weight: 400;
                font-size: 24px;
                line-height: 50px;
                color: #000000;

                @include s.media (s.$sm) {
                    font-size: 20px;
                }
            }
        }

        &__textcontent {
            max-width: 490px;
            width: 100%;
            height: 231px;
            border: 2px solid #000000;
            border-radius: 10px;
            font-family: inter-regular;
            font-style: normal;
            font-weight: 400;
            font-size: 24px;
            color: #000000;


            &::placeholder {
                font-family: inter-regular;
                font-style: normal;
                font-weight: 400;
                font-size: 24px;
                line-height: 30px;
                color: #000000;
            }
        }

        &__form {
            width: 500px;
            height: 535px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            @include s.media (s.$sm) {
                max-width: 500px;
                width: 100%;

                height: 450px;
            }
        }

        &__container {
            &-input {
