<script>
import axios from 'axios';
export default {
    data: () => ({
        icons: [
            {
                img: require('@/assets/img/contacts/github.svg'),
                href: 'https://github.com/Danil-Danya'
            },
            {
                img: require('@/assets/img/contacts/telegram.svg'),
                href: 'https://t.me/Strelokkk'
            },
            {
                img: require('@/assets/img/contacts/phone.svg'),
                href: 'tel:+998338909909'
            },
            {
                img: require('@/assets/img/contacts/gmail.svg'),
                href: 'mailto:danil.snip@gmail.com'
            }
        ]
    }),
    mounted () {
        this.$refs.form.onsubmit = e => {
            e.preventDefault();

            const URL = 'http://danil-sabitov.ru:3000';

            const getData = {
                name: this.$refs.name.value,
                email: this.$refs.email.value,
                text: this.$refs.message.value    
            }

            if (this.$refs.name.value === '' || this.$refs.email.value === '' || this.$refs.message.value === '') {
                alert('у вас есть пустое поле')
            }
            else {
                axios.post((URL), {
                    data: getData,
                })
                setTimeout(() => location.reload(), 1000);
            }
        }
    }
}
</script>