<template>
    <nav class="navbar">
        <div class="container">
            <div class="navbar__container flex-wrap">
                <div class="navbar__container-left">
                    <div class="logo__container">
                        <div class="logo__block">
                            <p class="logo__block-text">{{logo}}</p>
                        </div>
                        <h2 class="logo__title">Daniel</h2>
                    </div>
                    <div class="navbar__links">
                        <ul class="navbar__links-container">
                            <li v-for="link in navbarLinks" :key="link">
                                <a :href="link.link" class="navbar__links-text">{{link.text}}</a>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="navbar__container-right">
                    <ul class="navbar__container-icon">
                        <li v-for="icon in icons" :key="icon">
                            <a :href="icon.link">
                                <img :src="icon.img" alt="icon" class="navbar__icon scale__link">
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="navbar__mobile" ref="mobile">
            <div class="container">
                <div class="navbar__mobile-container">
                    <div class="navbar__mobile-right">
                        <div class="logo">
                            <div class="logo__container">
                                <div class="logo__block">
                                    <p class="logo__block-text">{{logo}}</p>
                                </div>
                                <h2 class="logo__title">Daniel</h2>
                            </div>
                        </div>
                    </div>
                    <div class="navbar__mobile-left">
                        <ul class="navbar__mobile-burger-menu" ref="ulContainer"
                        @click="show === false ? show = true : show = false;
                        animationChrestInBurgerMenu();">
                            <li class="navbar__mobile-line" ref="left"></li>
                            <li class="navbar__mobile-line" ref="center"></li>
                            <li class="navbar__mobile-line" ref="right"></li>
                        </ul>
                    </div>
                </div>
            </div>
            <div class="navbar__mobile-content" ref="content">
                <div class="container">
                    <div class="navbar__mobile-container-content">
                        <ul class="navbar__mobile-ul">
                            <li class="navbar__mobile-link" 
                             v-for="link in navbarLinks" 
                             :key="link">
                                <a :href="link.link" class="navbar__mobile-link" >{{ link.text }}</a>
                            </li>
                        </ul>
                        <ul class="navbar__container-icon">
                            <li v-for="icon in icons" :key="icon">
                                <a :href="icon.link">
                                    <img :src="icon.img" alt="icon" class="navbar__icon scale__link">
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </nav>
</template>

<style lang="scss">
    @use "@/assets/style/default.scss" as s;

    .logo {
        &__container {
            display: flex;
            align-items: flex-end;
            width: 250px;
            @include s.media (s.$md) {
                align-items: center;
            }
        }

        &__block {
            @include s.media (s.$md) {
                width: 70px;
                height: 70px;
            }
            max-width: 100px;
            width: 100%;
            height: 100px;
            background: #FCF200;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 10px;
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

            &-text {
                font-family: montsserat-bold;
                font-size: 40px;
                font-weight: 700;
                line-height: 49px;
                letter-spacing: 0em;
                text-align: left;
                @include s.media (s.$md) {
                    font-size: 36px;
                }
            }
        }

        &__title {
            font-family: montsserat-bold;
            margin: 0 0 0 10px;
            font-size: 40px;
            @include s.media (s.$md) {
                color: #fff;
                font-size: 36px;
            }
        }
    }
    .navbar {
        margin-top:20px;

        @include s.media (s.$md) {
            margin-top: 150px;
        }

        &__mobile {
            background: #000;
            height: 100px;
            width: 100%;
            position: fixed;
            z-index: 999;
            top: 0;
            right: 0;

            &-container {
                height: 100px;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                
                &-content {
                    width: 100%;
                    height: calc(100vh - 200px);
                    display: flex;
                    align-content: center;
                    justify-content: center;
                    flex-wrap: wrap;
                }
            }

            &__right {
                display: flex;
                align-items: center;
            }

            &-line {
                background: #fff;
                width: 30px;
                height: 2px;
                transition: 500ms;
            }
            &-burger-menu {
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                position: relative;
            } 

            &-content {
                width: 100%;
                background: rgba(0, 0, 0, 0.621);
                backdrop-filter: blur(4px);
                height: 100vh;
                position: fixed;
                z-index: 999;
                transform: translateX(-100%);
                transition: 500ms;
            }

            &-link {
                font-size: 36px;
                font-family: montsserat-bold;
                color: #fff !important;
                display: inline;
                text-align: center;
                height: 80px;
                display: flex;
                justify-content: center;
            }
            
            &-ul {
                display: flex;
                width: 100%;
                height: 100%;
                flex-direction: column;
                align-content: center;
                justify-content: center;
            }
        }

        
        &__container {
            @include s.media (s.$md) {
                display: none;
            }
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
            flex-wrap: wrap;
            &-left {
                width: 1060px;
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
            }
            &-icon {
                display: flex;
                width: 225px;
                justify-content: space-between;
            }
        }
        
        &__links{
            &-container {
                display: flex;
                width: 670px;
                justify-content: space-between;
            }
            &-text {
                font-family: montsserat-regular;
                font-size: 32px;
                color:#000;
                display: block; 
                position: relative;

                &::after {
                    content: '';
                    display: block;
                    position: absolute;
                    background: #000;
                    height: 2px;
                    width: 0px;
                    transition: width 0.3s;

                }
                &:hover {
                    &::after {
                        width: 100%;
                    }
                }
            }
        }
    }


</style>

<script>
export default {
    data: () => ({
        logo: '</>',
        show: false,
        navbarLinks: [
            {text:'История', link:'#history'},
            {text:'Работы', link:'#projects'},
            {text:'Умения', link:'#skills'},
            {text:'Контакты', link:'#contacts'}
        ],
        icons: [
            {
                img: require('@/assets/img/icon/github.svg'),
                link: 'https://github.com/Danil-Danya'
            },
            {
                img: require('@/assets/img/icon/telegram.svg'),
                link: 'https://t.me/Strelokkk'
            },
            {
                img: require('@/assets/img/icon/phone.svg'),
                link: 'tel:+998338709909'
            }
        ]
    }),
    mounted () {
        window.innerWidth > 960 ? this.$refs.mobile.style.display = 'none' : false;
    },
    methods: {
        animationChrestInBurgerMenu () {
            console.log(this.show);
            if (this.show === true) {
                this.$refs.center.style = 'display:none';

                this.$refs.content.style = `transform: translateX(0);`

                this.$refs.left.style = `transform:rotate(-45deg);
                                               position: absolute;
                                                       right: 50%;
                                                         top: 50%;`


                this.$refs.right.style = `transform:rotate(45deg);
                                               position: absolute;
                                                       right: 50%;
                                                         top: 50%;`
            }
            else {
                this.$refs.center.style = 'display:flex';

                this.$refs.content.style = `transform: translateX(-100%);`

                this.$refs.left.style = `transform:rotate(0);
                                               position: auto;
                                                  right: auto;
                                                    top: auto;`

                this.$refs.right.style = `transform:rotate(0);
                                               position: auto;
                                                  right: auto;
                                                    top: auto;`
            }
            
        }
    }
}
</script>