<style lang="scss" scoped>
@use "@/assets/style/default.scss" as s;
    .projects {
        &__container {
            width: 100%;
            overflow: hidden;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            @include s.media (s.$lg) {
                overflow: auto;
                padding: 0 15px;
            }

            &-minislider {
                padding: 40px 0;
                max-width: 2400px;
                width: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: space-between;
            }
        }

        &__minislide {
            transition: all 0.1s;
            animation: leftAndRight linear infinite 20s 0s;
            width: 2400px;
            display: flex;
            justify-content: space-between;
            margin: 40px 0;
            cursor: pointer;

            @include s.media (s.$lg) {
                animation: none;
                justify-content: center;
                margin: 0;
                max-width: 2400px;
                width: 100%;
            }

            &:nth-of-type(2) {
                animation: rightAndLeft linear infinite 20s 0s;
                
                @include s.media (s.$lg) {
                    animation: none;
                }
            }
        }

        &__more {
            max-width: 100%;
            width: 100%;
            height: 100px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #3C0061;

            &-link {
                font-family: montsserat-regular;
                font-size: 32px;
                line-height: 39px;
                color: #ffffff;

                @include s.media (s.$sm) {
                    font-size: 28px;
                }
            }
        }

        .item__scale:hover {
            transition: .2s;
            transform: scale(1.1);

            @include s.media (s.$md) {
                transform: scale(1);
            }
        }

        @keyframes leftAndRight {
            0%,100% {
                transform: translateX(100px);
            }
            50%{
                transform: translateX(-100px);
            }
        } 
        @keyframes rightAndLeft {
            0%,100% {
                transform: translateX(-100px);
            }
            50%{
                transform: translateX(100px);
            }
        }
    }

    .projects__minislide-img  {
        @include s.media (s.$lg) {
            max-width: 750px;
            width: 100%;
            margin-top: 30px;
        }
    }
</style>

